import { string, number } from "prop-types";

import styles from "./styles.module.scss";

const SearchSummary = ({ quantity, term }) => {
  return (
    <div className={styles.wrapper}>
      {quantity ? (
        <h1 className={styles.title}>
          <b>Vagas</b> encontradas
        </h1>
      ) : (
        <h1 className={styles.title}>
          <b>Busca</b> sem resultados
        </h1>
      )}
      {quantity >= 1 ? (
        <p className={styles.description}>
          {quantity === 1
            ? `${quantity} vaga encontrada`
            : `${quantity} vagas encontradas`}{" "}
          com "{term}"
        </p>
      ) : (
        <p className={styles.description}>Sem resultados para "{term}"</p>
      )}
    </div>
  );
};

SearchSummary.propTypes = {
  term: string.isRequired,
  quantity: number.isRequired,
};

export default SearchSummary;
