import React, { useEffect, useRef } from "react";
import { Form } from "@unform/web";
import * as Yup from "yup";
import { Link } from "react-router-dom";

import { Input } from "../../../../components/NewInput";
import useAxios from "../../../../hooks/useAxios";
import { Pencil, Lock } from "../../../../libs/icons";
import validationDictionary from "../../../../contants/validationDictionary";
import { useUserContext } from "../../../../contexts/User";

import FieldsGrid from "../../../authentication/components/FieldsGrid";

import styles from "./styles.module.scss";

const ChangeUserInfoForm = () => {
  const formRef = useRef(null);
  const { exec, loading } = useAxios();
  const { setUserData } = useUserContext();
  const { name, lastname, email } = JSON.parse(
    localStorage.getItem("@user_data")
  );

  const updateOnBlur = () => {
    if (loading) return;
    formRef.current.submitForm();
  };

  const handleSubmit = async (data) => {
    try {
      formRef.current.setErrors({});

      const schema = Yup.object().shape({
        name: Yup.string()
          .min(3, validationDictionary.INVALID_NAME)
          .required(validationDictionary.INVALID_NAME),
        lastname: Yup.string()
          .min(3, validationDictionary.INVALID_LASTNAME)
          .required(validationDictionary.INVALID_LASTNAME),
      });

      await schema.validate(data, {
        abortEarly: false,
      });

      const response = await exec({
        useCredentials: true,
        method: "POST",
        url: "/user/update",
        data: {
          name: data.name,
          lastname: data.lastname,
        },
      });

      const storage = JSON.parse(localStorage.getItem("@user_data"));
      const updatedStorage = {
        ...storage,
        name: response.data.name,
        lastname: response.data.lastname,
      };

      setUserData(updatedStorage);
      localStorage.setItem("@user_data", JSON.stringify(updatedStorage));
    } catch (err) {
      const validationErrors = {};
      if (err instanceof Yup.ValidationError) {
        err.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });
        formRef.current.setErrors(validationErrors);
      }
    }
  };

  useEffect(() => {
    setUserData({ name, lastname, email });
  }, [name, lastname, email]);

  return (
    <Form
      ref={formRef}
      initialData={{ name, lastname, email }}
      onSubmit={handleSubmit}
    >
      <FieldsGrid>
        <Input
          label="Nome"
          name="name"
          placeholder="Digite seu nome"
          type="text"
          blurCallback={updateOnBlur}
          disabled={loading}
          icon={<Pencil color="#8F9BB3" />}
        />
        <Input
          label="Sobrenome"
          name="lastname"
          placeholder="Digite seu sobrenome"
          type="text"
          blurCallback={updateOnBlur}
          disabled={loading}
          icon={<Pencil color="#8F9BB3" />}
        />
        <Input
          label="E-mail"
          name="email"
          placeholder={email}
          type="text"
          disabled
          icon={<Lock color="#8F9BB3" />}
        />
      </FieldsGrid>
      <p className={styles.emailChangeMessage}>
        <Link to="/contato">Contate a Inklua</Link> para editar seu e-mail
      </p>
    </Form>
  );
};

export default ChangeUserInfoForm;
